  .md\:min-w-\[35rem\] {
    min-width: 35rem;
  }

  .md\:flex-row {
    flex-direction: row;
  }

  .md\:space-y-0 > :not([hidden]) ~ :not([hidden]) {
    --tw-space-y-reverse: 0;
    margin-top: calc(0px * calc(1 - var(--tw-space-y-reverse)));
    margin-bottom: calc(0px * var(--tw-space-y-reverse));
  }

  .md\:border-l-2 {
    border-left-width: 2px;
  }

  .md\:pl-7 {
    padding-left: 1.75rem;
  }
}
